/* Existing styles up to 768px */
body {
  font-family: Franklin Gothic;
}

.p-demo {
  margin-left: 10%;
  color: #ff1d18;
}

.line {
  margin-left: 10%;
  font-size: 20px;
  font-weight: bolder;
  color: rgb(77, 75, 75);
}

.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  gap: 20px;
}

/* Existing styles */
h3 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 20px;
}

hr {
  width: 80%;
  border: 1px solid #ff1d18;
}

.image-container {
  margin-left: 10%;
  border-radius: 5px;
  overflow: hidden;
  width: 40%;
  height: 200px;
  scroll-behavior: smooth;
  position: relative;
  float: right;
  margin-bottom: 20%;
}

.carimg {
  border-radius: 30px;
  margin-top: -15px;
  width: 100%;
  height: auto;
  transition: transform 4s ease;
}

.image-container:hover img {
  transform: translateY(calc(-100% + 200px));
  cursor: grab;
}

.image-container:active img {
  cursor: grabbing;
}

.box {
  background-color: rgb(253, 253, 253);
  border-radius: 30px;
}

.left-section {
  margin-left: 10%;
  margin-top: -20%;
  text-align: left;
  padding: 10px;
  width: 40%;
  float: left;
}

.left-section h3 {
  margin-top: 0;
}

.left-section a {
  text-align: center;
  margin-left: 15%;
  border: 2px solid #ff1d18;
  background-color: #ff1d18;
  padding: 10px;
  width: 30%;
  border-radius: 5px;
  display: block;
  margin-bottom: 10px;
  color: white;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
}

.left-section a:hover {
  color: #ff1d18;
  background-color: white;
}

.left-section h2 {
  font-size: 16px;
  font-weight: bold;
}

.left-section h3 {
  font-size: 14px;
  line-height: 1.5;
}

/* Project container */
.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 20px;
}

/* Each project section */
.left-section {
  flex: 0 1 40%;
  padding: 20px;
}

.image-container {
  flex: 0 1 40%;
  text-align: right;
  padding: 20px;
}

/* Images within each project section */
.image-container img {
  max-width: 100%;
  height: auto;
}

/* Media query for max-width 769px */
@media screen and (max-width: 769px) {
  .projects {
    display: block;
    margin: 5%;
  }
  .image-container,
  .left-section {
    margin-left: -8%;
    width: 100%;
    text-align: center;
  }
  .image-container {
    float: none;
    margin-right: 0;
  }
  .left-section {
    margin-left: 0;
  }
  .left-section a {
    margin-left: -8%;
    width: 100%;
  }
  .left-section h2,
  .left-section h3 {
    margin-left: -4%;
    text-align: center;
  }
  hr {
    margin-bottom: 10%;
  }
}

/* Media query for max-width 510px */
@media screen and (max-width: 510px) {
  .projects {
    display: block;
    margin: 5%;
  }
  .image-container,
  .left-section {
    margin-left: 0%;
    width: 90%;
    text-align: center;
  }
  .image-container {
    float: none;
    margin-right: 0;
  }
  .left-section {
    margin-left: 0;
  }
  .left-section a {
    margin-left: 0%;
    width: 80%;
  }
  .left-section h2,
  .left-section h3 {
    margin-left: -10%;

    text-align: center;
  }
  hr {
    margin-bottom: 10%;
  }
}
